@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,700);
body {
  margin: 0px auto;
  padding: 10px;
  text-align: center;
  letter-spacing: 3px;
  max-width: 570px;
  background-color: #d1d8e0;
  font-family: 'Roboto Mono', monospace;
}

#quote-box {
  padding-top: 40px;
}

h1 {
  padding-top: 70px;
  letter-spacing: 5px;
  font-weight: normal;
}


.github {
  margin-top: 150px;
}
.fa-github {
  padding: 5px;
  color: #38A1F3;
}
.fa-github:hover {
  color: black;
}
/* Buttons */
.buttons-container {
  outline: none; 
}

.buttons-container>a {
  cursor: pointer;
  padding: 7px;
  font-size: 1.875em;
  margin: 35px;
  color: #38A1F3;
  border-radius: 5px;
}

#new-quote:hover, #tweet-quote:hover {
  box-shadow: 2px 2px 1px 0px rgba(0, 0, 0, .5);
}


/* Quote */
.quote-container {
  box-shadow: 2px 2px 2px 2px rgba(0, 0, .5, .5);
  margin-top: 90px;
  padding: 15px;
  border-radius: 5px;
  background-color:  #a5b1c2;
}

#text {
  font-size: 1.500em;
}

#author {
  font-size: 1.250em;
}


/* Loading... paragraph */
.loading {
  font-size: 1.875em;
  padding-top: 200px;
  letter-spacing: 4px;
}


